    <!-- 左侧对话区 -->
    <div class="chat-section">
      <div class="chat-header">
        <span class="chat-icon">🤖</span>
        <span class="chat-title">Conflict Simulation</span>
      </div>
      <div class="chat-subtitle">智能模拟对话 · 个性化学习指导</div>
      <div class="chat-history">
        <div
          v-for="(msg, idx) in messages"
          :key="idx"
          :class="['chat-bubble', msg.role, { 'user': msg.role === 'user', 'bot': msg.role === 'bot' }]"
        >
          <span v-if="msg.role === 'bot'" class="bubble-icon">🤖</span>
          <span v-if="msg.role === 'user'" class="bubble-icon-user"></span>
          <template v-if="msg.role === 'bot' && !msg.text && loading">
            <span class="loading-spinner-in-bubble" title="模型思考中"></span>
          </template>
          <template v-else>
