          </template>
        </div>
        <!-- <div class="chat-time">{{ lastTime }}</div> -->
      </div>
      <div class="chat-input-wrapper">
        <input
          class="chat-input"
          placeholder="请输入你的回复"
          v-model="input"
          @keyup.enter="sendMessage"
          :disabled="loading"
        />
        <button class="chat-send-btn" @click="sendMessage" :disabled="loading">
          <svg width="24" height="24" fill="none" viewBox="0 0 24 24"><path d="M3 20L21 12L3 4V10L17 12L3 14V20Z" fill="#a78bfa"/></svg>
        </button>
