            </div>
            <div class="teach-item teach-green">
              <span class="teach-item-icon">⭐</span>
              <span class="teach-item-title">Proposal：</span>
              <span class="teach-item-content">你已经完成了今天的学习目标！保持这种学习节奏。</span>
            </div>
            <div class="teach-item teach-blue">
              <span class="teach-item-icon">📈</span>
              <span class="teach-item-title">Concession：</span>
              <span class="teach-item-content">注意到你在分析问题时逻辑清晰，建议继续保持这种思考方式。</span>
            </div>
            <div class="teach-item teach-purple">
              <span class="teach-item-icon">🎯</span>
              <span class="teach-item-title">Promise：</span>
              <span class="teach-item-content">可以尝试多角度思考问题，这有助于加深对知识的理解。</span>
            </div>
