        </div>
      </div>
      <!-- 学习小贴士 -->
      <div class="card study-tips study-tips-yellow">
        <div class="study-tips-title"><span class="study-tips-icon">💡</span>学习小贴士</div>
        <div class="study-tips-content"><span class="study-tips-icon">📒</span>每次学习后花几分钟总结要点，有助于知识的长期记忆。</div>
