<script>
import axios from 'axios';
export default {
  data() {
    return {
      input: '',
      messages: [
        {
          role: 'bot',
          text: '你好！我是你的Conflict Simulation助手。我可以帮助你模拟和分析各种冲突场景，并提供个性化的指导。'
        }
      ],
      loading: false,
      scoreFeedback: null,
      teachFeedback: [],
      scoreFeedbackLoading: false, // 新增
      teachFeedbackLoading: false  // 新增
    }
  },
  computed: {
    lastTime() {
      return '17:25';
    }
  },
  methods: {
    async sendMessage() {
      const text = this.input && this.input.trim();
      if (!text || this.loading) return;
      const title = "零花钱的使用"; // 可根据实际业务动态获取
      this.messages.push({ role: 'user', text });
      this.input = '';
      this.loading = true;
      this.$nextTick(() => {
        const chatHistory = this.$el.querySelector('.chat-history');
        if (chatHistory) chatHistory.scrollTop = chatHistory.scrollHeight;
      });
      // 先插入一个空的机器人气泡
      const botMsg = { role: 'bot', text: '' };
      this.messages.push(botMsg);
      this.$nextTick(() => {
        const chatHistory = this.$el.querySelector('.chat-history');
        if (chatHistory) chatHistory.scrollTop = chatHistory.scrollHeight;
      });
      let botResponse = '';
      try {
        const response = await fetch('http://dify.ai-role.cn/v1/chat-messages', {
          method: 'POST',
          headers: {
            'Authorization': 'Bearer app-1FfMKOZZ12LF8HXlpXdTopPG',
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({
            query: text,
            inputs: {
              training: "引导性对话",
              title: title
            },
            response_mode: "streaming",
            user: "abc-123"
          })
        });
        if (!response.body || !window.ReadableStream) {
          // fallback: 不是流式响应
          const data = await response.json();
          botMsg.text = data.answer || data.response || '（大模型无回复）';
          botResponse = botMsg.text;
          this.loading = false;
          // 机器人回复后，拉取反馈
          this.get_score_feedback(title, text, botResponse);
          this.get_teach_feeedback(title, text, botResponse);
          return;
        }
        const reader = response.body.getReader();
        const decoder = new TextDecoder('utf-8');
        let done = false;
        let buffer = '';
        while (!done) {
          const { value, done: doneReading } = await reader.read();
          done = doneReading;
          if (value) {
            buffer += decoder.decode(value, { stream: true });
            // 以换行分割流式片段
            let lines = buffer.split('\n');
            buffer = lines.pop();
            for (const line of lines) {
              if (!line.trim()) continue;
              if (line.startsWith('event: ping')) continue; // 跳过心跳包
              // 兼容 SSE 格式: data: xxx
              let content = line;
              if (line.startsWith('data:')) content = line.replace(/^data:\s*/, '');
              try {
                const json = JSON.parse(content);
                if (json.answer) {
                  botMsg.text += json.answer;
                } else if (json.response) {
                  botMsg.text += json.response;
                } else if (typeof json === 'string') {
                  botMsg.text += json;
                }
              } catch {
                botMsg.text += content;
              }
              this.$forceUpdate();
              this.$nextTick(() => {
                const chatHistory = this.$el.querySelector('.chat-history');
                if (chatHistory) chatHistory.scrollTop = chatHistory.scrollHeight;
              });
            }
          }
        }
        // 处理最后一段
        if (buffer && buffer.trim()) {
          try {
            const json = JSON.parse(buffer);
            if (json.answer) {
              botMsg.text += json.answer;
            } else if (json.response) {
              botMsg.text += json.response;
            } else if (typeof json === 'string') {
              botMsg.text += json;
            }
          } catch {
            botMsg.text += buffer;
          }
          this.$forceUpdate();
        }
        botResponse = botMsg.text;
      } catch (e) {
        console.error('API请求错误:', e);
        botMsg.text = '机器人服务暂时不可用，请稍后再试。';
        botResponse = botMsg.text;
      } finally {
        this.loading = false;
        // 机器人回复后，拉取反馈
        this.get_score_feedback(title, text, botResponse);
        this.get_teach_feeedback(title, text, botResponse);
      }
    },
    async get_score_feedback(title, user_response, bot_response) {
      this.scoreFeedbackLoading = true; // 新增
      try {
        const res = await axios.post('http://dify.ai-role.cn/v1/chat-messages', {
          query: 'score feedback',
          inputs: {
            title:title,
            user_response:user_response,
            bot_response:bot_response
          },
          response_mode: "blocking",
          user: "abc-123"
        }, {
          headers: {
            'Authorization': 'Bearer app-XPpxMLpT4EmwJ2VwXwvmOBPt',
            'Content-Type': 'application/json'
          }
        });
        console.log(res.data.answer)
        this.scoreFeedback = res.data.answer;
      } catch (e) {
        this.scoreFeedback = { reason: '评分反馈获取失败' };
      } finally {
        this.scoreFeedbackLoading = false; // 新增
      }
    },
    async get_teach_feeedback(title, user_response, bot_response) {
      this.teachFeedbackLoading = true; // 新增
      try {
        const res = await axios.post('http://dify.ai-role.cn/v1/chat-messages', {
          query: 'teach feedback',
          inputs: {
            title: title,
            user_response: user_response,
            bot_response: bot_response
          },
          response_mode: "blocking",
          user: "abc-123"
        }, {
          headers: {
            'Authorization': 'Bearer app-YhRXu8JFuHs9deDyR8AJwYrC',
            'Content-Type': 'application/json'
          }
        });
        // 假设返回字符串 answer
        const str = res.data.answer || '';
        console.log('教学反馈原始数据:', res.data.answer);
        
        // 分割字符串为4个卡片
        const cardDefs = [
          { title: 'NVC', icon: '💡', colorClass: 'teach-yellow' },
          { title: 'Proposal', icon: '⭐', colorClass: 'teach-green' },
          { title: 'Concession', icon: '📈', colorClass: 'teach-blue' },
          { title: 'Promise', icon: '🎯', colorClass: 'teach-purple' }
        ];
        
        // 尝试多种匹配方式
        let result = [];
        
        // 方式1: 使用正则表达式匹配
        const regex = /(NVC|Proposal|Concession|Promise)：(.*?)(?=(NVC|Proposal|Concession|Promise)：|$)/gs;
        let match;
        while ((match = regex.exec(str)) !== null) {
          const idx = cardDefs.findIndex(c => c.title === match[1]);
          if (idx !== -1) {
            result[idx] = {
              ...cardDefs[idx],
              content: match[2].trim()
            };
          }
        }
        
        // 方式2: 如果正则匹配失败，尝试按行分割
        if (result.length === 0) {
          const lines = str.split('\n').filter(line => line.trim());
          lines.forEach(line => {
            const colonIndex = line.indexOf('：');
            if (colonIndex > 0) {
              const title = line.substring(0, colonIndex).trim();
              const content = line.substring(colonIndex + 1).trim();
              const idx = cardDefs.findIndex(c => c.title === title);
              if (idx !== -1) {
                result[idx] = {
                  ...cardDefs[idx],
                  content: content
                };
              }
            }
          });
        }
        
        console.log('解析后的教学反馈:', result);
        
        // 保证顺序和完整性
        this.teachFeedback = cardDefs.map((def, i) => result[i] || { ...def, content: '暂无反馈内容' });
        console.log(this.teachFeedback)
      } catch (e) {
        this.teachFeedback = [];
      } finally {
        this.teachFeedbackLoading = false; // 新增
      }
    }
  }
}
</script>

